.updateProfileContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0%;
}
.updateProfileInputBox {
    background-color: white;
    width: 35vw;
    height: 85vh;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
}

.updateProfileInputBox>h2 {
    padding : 1.3vmax;
    text-align: center;
    font : 600 1.8vmax "Roboto";
    color : rgb(86, 86, 86);
    padding : 1.3vmax;
    border-bottom : 1px solid rgba(0,0,0,0.205);
    width: 50%;
    margin : auto;
}

.updateProfileForm {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
}

.updateProfileForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.updateProfileForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax Roboto;
    outline: none;
}

.updateProfileForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

#updateProfileImage>img {
    width: 3vmax;
    border-radius: 100%;
    margin-right: 1vmax;
}

#updateProfileImage>input {
    display: flex;
    padding: 0%;
}

#updateProfileImage>input::file-selector-button {
    cursor: pointer;
    width: 100%;
    height: 5vh;
    z-index: 2;
    border: none;
    margin: 0%;
    font: 400 0.8vmax "Roboto";
    transition: all 0.5s;
    padding: 0 1vmax;
    color: rgba(0, 0, 0, 0.625);
    background-color: rgb(255, 255, 255);
}

#updateProfileImage > input::file-selector-button:hover {
    background-color: rgb(247, 247, 247);
}

.updateProfileBtn {
    border: none;
    background-color: lightseagreen;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
    margin: 2vmax 0;
}

.updateProfileBtn:hover {
    background-color: rgb(34, 163, 157);
}

.shiftToLeft {
    transform: translateX(-100%);
}

.shiftToNeutral {
    transform: translateX(0%);
}

.shiftToNeutralForm {
    transform: translateX(0%) translateY(-100%);
}

.shiftToRight {
    transform: translateX(100%);
}

@media screen and (max-width : 600px) {
    .updateProfileContainer {
        background-color: white;
    }
    .updateProfileInputBox {
        width: 80vw;
        height: 100vw;
    }
    .updatedProfileForm > div > input {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax "Roboto";
    }

    .updatedProfileForm > div > svg {
        font-size: 2.0vmax;
    }

    #updateProfileImage > img {
        width: 8vmax;
        border-radius: 100%;
    }

    #updateProfileImage >input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }

    #updateProfileBtn
    {
        font: 300 1.9vmax "Roboto";
        padding: 1.8vmax;
    }
}