.searchBox {
    width : 100vw;
    height : 100vh;
    max-width : 100%;
    display : flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(231,231,231);
    position : fixed;
    top : 0%;
    left : 0;
}

.searchBox > input[type = "text"] {
    box-shadow : 0 0 5px rgba(0,0,0,0.274);
    background-color: white;
    border : none;
    color : rgba(0,0,0,0.637);
    padding : 1vmax 2vmax;
    width : 50%;
    outline : none;
    border-radius :  50px;
    font : 300 1.1vmax cursive;
    box-sizing: border-box;
    height : 8%;
}

.searchBox > input[type = "submit"] {
    height : 8%;
    border-radius : 50px;
    background-color: lightseagreen;
    border : none;
    padding : 1vmax;
    width : 10%;
    font : 300 1.1vmax "Roboto";
    cursor: pointer;
    color : white;
    transition: all 0.5s;
    margin-left: 1vmax;
}

.searchBox > input[type="submit"]:hover {
    background-color: rgb(30, 151, 145);
}